@import "../../assets/sass/variabels.scss";
.SingleCoin_wrraper {
  position: relative;
  padding: 0 1rem;
}
.SingleCoin {
  @media (max-width: 700px) {
    flex-direction: column;
  }
  max-width: 100%;
  min-height: 330px;
  display: flex;
  justify-content: space-between;

  font-size: 1.6rem;
  padding: 2rem;
  color: $color-white;
  background-color: $color-primary;
  margin: 5rem 0;
  position: relative;
  border-radius: 3px;
  transition: all 0.3s;
  box-shadow: 5px 5px 20px 0px rgba(0, 0, 0, 0.5);
  &:hover {
    box-shadow: 2px 2px 10px 0px rgba(0, 0, 0, 0.5);
    transform: scale(0.99);
  }

  & > * {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: flex-end;
  }

  // &_rank{
  //     order:5 ;
  //     font-size: 2rem;
  //     align-items: center;
  // }
  &_tradingview {
    order: -2;
    flex-basis: 55%;
    display: flex;
    align-items: flex-start;
  }
}
.price {
  &_change {
    direction: rtl;

    &--num {
      display: inline-flex;
      flex-direction: row-reverse;
      direction: ltr;
      font-size: 2rem;
      margin-right: 1rem;
      font-weight: bold;
    }
  }
}
.trash_icon {
  font-size: 11px;
  color: $color-white;
}
.SingleCoin_wrraper_trash {
  background-color: $color-red;
  width: 23px;
  height: 23px;
  position: absolute;
  right: -4px;
  bottom: -10px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 50%;
  cursor: pointer;
  z-index: 50;
}

.sample-exit-active {
  opacity: 0;
  transform: scale(0.1);
  transition: all 200ms ease-in-out;
}
.sample-exit-done {
  opacity: 0;
  transform: scale(0);
  //   transition: all 100ms ease-in-out;
}
.SingleCoin_texts {
  display: flex;
  align-items: stretch;
  justify-content: space-between;
  flex-direction: row;
  flex-basis: 40%;
  // @media (max-width: 700px) {
  //   width: 100%;
  // }
}
.SingleCoin_name,
.SingleCoin_price {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: flex-end;
}
